@media screen and (max-width: 1199px) {
  .mobile-menu {
    position: fixed;
    top: 0;
    left: 0%;
    z-index: 999;
    width: 100vw;
    height: 100vh;

    padding-left: 0;
    padding-right: 0;
    padding-top: 100px;
    padding-bottom: 63px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    background-color: $mobile-menu-bg-color;

    transform: translateX(100%);
    transition: transform $cubic;

    &.is-open {
      transform: translateX(0);
    }
  }

  .logo-image {
    position: absolute;
    top: 12px;
    left: 31px;
  }

  .mobile-menu__close-btn {
    position: absolute;
    top: 16px;
    right: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

    line-height: none;
    background-color: transparent;
    border: none;
    outline: none;

    cursor: pointer;
  }

  .mobile-menu__close-icon {
    fill: $primary-white-color;
    transition: fill $cubic;

    .mobile-menu__close-btn:hover &,
    .mobile-menu__close-btn:focus & {
      fill: $primary-black-color;
    }
  }

  .mobile-menu__list {
    text-align: center;
  }

  .mobile-menu__item:not(:last-child) {
    margin-bottom: 32px;
  }

  .mobile-menu__link {
    font-weight: 700;
    font-size: 32px;
    line-height: 1.22;

    color: $primary-white-color;

    transition: color $cubic;

    &:hover,
    &:focus {
      color: $primary-black-color;
      outline: none;
    }
  }
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    padding-top: 140px;
  }

  .logo-image {
    top: 20px;
    left: 32px;
  }

  .mobile-menu__close-btn {
    top: 32px;
    right: 32px;
  }

  .mobile-menu__item:not(:last-child) {
    margin-bottom: 40px;
  }

  .mobile-menu__link {
    font-size: 48px;
    line-height: 1.23;
  }

  .buy-now-btn.mobile-menu__buy-now-btn {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .mobile-menu {
    display: none;
  }
}
