.gallery{
    display: flex;
    position: relative;
    @media screen and (max-width:479px) {
      height: 130.03px;
   }
   @media screen and (min-width:480px) {
      height: 163.5px;
   }
   @media screen and (min-width:768px) {
      height: 327px;
   }
    @media screen and (min-width:1200px) {
      height: 422px;
}
    }
    
.gallery__img{
    display: block;
    width: 100%;
    height: 100%;
    border-radius:20px;
    object-fit: cover;
}
.gallery__item{
    position: absolute;
    display: block;
    top: 0;
    left: 50%;
    transform: translateX(-50%);  
    @media screen and (max-width:479px) {
        width: calc(100% - 41px);
        height: 130.03px;
    }
    @media screen and (min-width:480px) {
        width: 352px;
        height: 163.5px;
    }
    @media screen and (min-width:768px) {
        width: 704px;
        height: 327px;
    }
    @media screen and (min-width:1200px) {
        width: 1013px;
        height: 422px;
    }
}

//only animation CSS
@keyframes gallerryAnimFirstImg{
   0%{
      opacity: 100%;
   }
  25%{
   opacity: 100%;
  }
  50%{
   opacity: 0%;
  }
  75%{
   opacity: 100%;
  }
  100%{
   opacity: 100%;
  }
  }
@keyframes gallerryAnimSecondImg{
   0%{
      opacity:0;
   }
   25%{
      opacity: 100%;
   }
   50%{
      opacity:100%;
    }
    75%{
      opacity: 0;
    }
    100%{
       opacity:0;
    }
  }
  @keyframes gallerryAnimThirdImg{
   0%{
      opacity: 0;
   }
   25%{
      opacity: 0;
   }
    50%{
       opacity: 100%;
    }
    75%{
      opacity: 0;
    }
    100%{
       opacity:0;
    }
  }
  @keyframes gallerryAnimFourthImg{
   0%{
      opacity: 0;
   }
   
   25%{
      opacity: 0;
   }
    50%{
       opacity: 0;
    }
    75%{
      opacity: 100%;
    }
    100%{
      opacity: 0;
    }
  }
 
.gallery__item:nth-child(1){
    animation-name: gallerryAnimFirstImg;
    animation-duration: 12s;
    animation-direction: normal;
    animation-timing-function:ease-in-out;
    animation-iteration-count: infinite;
}
.gallery__item:nth-child(2){
   
    animation-name: gallerryAnimSecondImg;
    animation-duration: 12s;
    animation-direction: normal;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
}
.gallery__item:nth-child(3){
    animation-name: gallerryAnimThirdImg;
    animation-duration: 12s;
    animation-direction: normal;
    animation-timing-function:ease-in-out;
    animation-iteration-count: infinite;
}
.gallery__item:nth-child(4){
    animation-name: gallerryAnimFourthImg;
    animation-duration: 12s;
    animation-direction:normal;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;

}